import React, { Component } from 'react'
// @ts-ignore
import Coordenadoras from './imagens/coodenadoras.png';
import './../../css/styleMenu.css';
import './evangelizacao.css'

export class Infancia extends Component {
    render () {
        return (
            <div>
                <div id="evangeliza">
                    <div className="img">
                        <a target="_parent" href="/">
                            <img src={Coordenadoras} alt="Fjords"/>
                        </a>
                            <div className="desc">Essas so as coordenadoras, venham conhec-las.</div>
                    </div>
                        
                        <table className="table">
                            <tr>
                                <th scope="col"><p className="n"> A Evangelizao Esprita Infanto-juvenil  Meimei de nossa Casa Esprita acontece todos os domingos das 9h s 11h. Aliada  a ela oferecemos, no mesmo horrio, a Evangelizao de Adultos para os pais e  familiares/amigos dos evangelizandos.<br />
                                    <br />
                                    A nossa Mocidade Esprita ocorre aos  domingos, das 17h s 18:30, antes das reunies pblicas.<br />
                                    <br />
                                    Nas atividades de domingo, contamos com  cinco turmas: <br />
                                    Maternal (3 e 4 anos),  Jardim(5  e 6 anos), 1 ciclo (7 e 8 anos), 2 ciclo (9 e 10 anos) e 3 ciclo (11 e 12  anos).<br />
                                    <br />
                                    Iniciamos com a Alegria Crist,  aplicao de passe magntico e gua fluida para todos. Aps a prece inicial, os  evangelizandos e adultos encaminham-se para as salas de evangelizao para  usufrurem de aulas baseadas nas Apostilas da FEB. <br />
                                    <br />
                                    Todo ltimo domingo do ms temos   Hora de Arte, um trabalho desenvolvido  por irmos amigos, que estimulam em nossas crianas o sentido do belo e  criativo. <br />
                                    <br />
                                    Realizamos passeios no ms de outubro, em comemorao ao dia da  criana e aniversrio da CEFE; fazemos o dia da famlia; festa junina dos  valores, comemoraes natalinas, etc.<br />
                                    <br />
                                    Ao final dos trabalhos, ofertamos Sopa  Fraterna para ser deliciada na CEFE e/ou levada para casa. <br />
                                    <br />
                                    Este alimento j  comea seu preparo (corte de verduras) no sbado anterior a evangelizao, a  partir das 14:30h. Mos amigas nos auxiliam muito! <br />
                                    <br />
                                    Contamos com um universo de 70 crianas  que nos abenoam a cada encontro. Ser evangelizador  uma graa divina!<br />
                                    <br />
                                    <span className="c">Voc que ainda no conhece uma maneira  maravilhosa de auxiliar algum, venha, <br />
                                        EVANGELIZE E COOPERE COM JESUS.</span></p></th>
                            </tr>
                        </table>
                </div>
            </div>
        )
    }
}

export default Infancia;


