import React from 'react';
import './../css/howNoticias.css';
import { Link } from 'react-router-dom';
import ListaSemanaPalestra from './ListaSemanaPalestra';

const primeiraNoticia = [
        { route: "/palestra", label: "Escala de Oradores" },
];

export default class TresNoticiasRapidas extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            primeiraNoticia: '',
            segundaNoticia: '',
            terceiraNotica: ''
        }
    }

    render(){
        return (
            <div className="containerHowNoticias">
                <div className="primeiraNoticia">
                    <ListaSemanaPalestra/>
                        {primeiraNoticia.map((link, a) => 
                            // @ts-ignore
                            <Link to={'/palestra'} target="_parent" key={a}>
                                <div className="link">Veja aqui a Escala de Oradores</div>
                            </Link>
                        )}
                </div>
                <div className="segundaNoticia"><p className="small">O III Intercmbio de Trabalhadores da Arte no Movimento Esprita ser realizado no dia 
                    1 de setembro, s 8h30, na sede da Federao Esprita do Rio Grande do Sul (FERGS) promotora do evento.  A Arte a Servio do Bem ser o tema abordado.
                    </p></div>
                <div className="terceiraNoticia"><p className="small">Promovido pela Federao Esprita do Estado da Bahia (FEEB) com apoio da Federao Esprita 
                    Brasileira (FEB) e do CR 13 (Conselho Regional), no perodo de 23 a 26 de agosto ser realizado o 25 Frum Esprita de Itanhm (BA) 
                    com o tema A Gnese.</p></div>
            </div>
        )
    }
}


